html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Montserrat-400.woff2") format("woff2"), url("../fonts/Montserrat-400.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Inter-400.woff2") format("woff2"), url("../fonts/Inter-400.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Inter-500.woff2") format("woff2"), url("../fonts/Inter-500.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Inter-700.woff2") format("woff2"), url("../fonts/Inter-700.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 900;
  src: url("../fonts/Inter-900.woff2") format("woff2"), url("../fonts/Inter-900.woff") format("woff");
  font-display: swap;
}
.page__body {
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #e4e5ea;
  margin: 0;
}

.container {
  width: 100%;
  max-width: 320px;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  background-color: #000000;
}
@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding: 46px 148px 0;
  }
}

.main {
  flex-grow: 1;
  padding: 40px 16px 55px;
  background-image: url("../img/background-img-mob.png");
  max-height: 445px;
  background-repeat: no-repeat;
  background-position: top center;
  background-color: #000000;
}
@media screen and (min-width: 1440px) {
  .main {
    background-image: none;
    padding: 90px 0 0;
    margin-bottom: 70px;
    display: flex;
    justify-content: space-between;
    max-height: none;
  }
}

.header {
  position: relative;
}
@media screen and (min-width: 1440px) {
  .header {
    display: flex;
    padding: 14px 0 4px;
    margin: 0 auto;
  }
}

.header__logo {
  display: none;
}
@media screen and (min-width: 1440px) {
  .header__logo {
    display: block;
  }
}

@media screen and (min-width: 1440px) {
  .header__nav {
    margin: 0 136px 0 50px;
  }
}

.header::before {
  position: absolute;
  content: "";
  width: 260px;
  height: 260px;
  background-color: #833ab4;
  border-radius: 50%;
  opacity: 0.05;
  top: 270%;
  left: 80%;
  box-shadow: 0 0 30px 30px #833ab4;
}
@media screen and (min-width: 1440px) {
  .header::before {
    width: 260px;
    height: 260px;
    opacity: 0.15;
    box-shadow: 0 0 50px 50px #833ab4;
    top: -202px;
    left: 51%;
    transform: translateX(-50%);
  }
}

.address {
  padding-top: 10px;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  font-style: normal;
}
@media screen and (min-width: 1440px) {
  .address {
    padding-top: 0;
  }
}

.cards,
.cards__wrapper,
.cards__title,
.cards__caption {
  display: none;
}
@media screen and (min-width: 1440px) {
  .cards,
.cards__wrapper,
.cards__title,
.cards__caption {
    display: block;
  }
}

.cards {
  /*display: flex;
  flex-wrap: wrap;*/
}
@media screen and (min-width: 1440px) {
  .cards {
    column-count: 2;
    column-gap: 30px;
    width: 300px;
  }
}

@media screen and (min-width: 1440px) {
  .cards__wrapper {
    margin-bottom: 30px;
  }
}

@media screen and (min-width: 1440px) {
  .cards__title {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    text-align: right;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    margin: 0 0 6px;
  }
}

@media screen and (min-width: 1440px) {
  .cards__caption {
    font-family: "Montserrat", "Arial", sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-align: right;
  }
}

.contact {
  display: none;
}
.contact--footer {
  display: block;
}
@media screen and (min-width: 1440px) {
  .contact {
    display: flex;
    position: relative;
  }
  .contact--footer {
    flex-direction: column;
    align-items: end;
  }
}

.contact__item {
  cursor: pointer;
}

.contact__phone {
  display: block;
  margin-bottom: 8px;
  font-size: 16px;
  line-height: 19px;
}
@media screen and (min-width: 1440px) {
  .contact__phone {
    margin-bottom: 20px;
  }
  .contact__phone--header {
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-align: right;
    margin-right: 46px;
  }
}

.contact__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  gap: 0 10px;
}
@media screen and (min-width: 1440px) {
  .contact__list {
    margin-bottom: 20px;
  }
  .contact__list--header {
    gap: 0 30px;
  }
}

.contact__icon {
  width: 24px;
  height: 24px;
}
@media screen and (min-width: 1440px) {
  .contact__icon {
    width: 20px;
    height: 20px;
  }
}

@media screen and (min-width: 1440px) {
  .contact--header::after {
    position: absolute;
    content: "";
    border-radius: 50%;
    background: radial-gradient(64% 64% at 27.25% 26.5%, rgba(236, 208, 255, 0.64) 0%, rgba(161, 93, 207, 0.24) 59.67%, rgba(118, 49, 164, 0.21) 78.59%, rgba(86, 24, 128, 0.23) 100%);
    box-shadow: 3px 6px 27px -10px rgba(177, 118, 217, 0.25);
    filter: blur(3px);
    backdrop-filter: blur(2px);
    top: -64px;
    left: -120px;
    width: 60px;
    height: 60px;
  }
}

.copyright {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
}

.copyright__link {
  color: #e4e5ea;
  text-decoration: none;
  margin-top: 5px;
}

.footer {
  padding: 45px 16px 0;
}
@media screen and (min-width: 1440px) {
  .footer {
    position: relative;
    padding: 54px 0 20px;
    display: flex;
    justify-content: space-between;
  }
}

.footer__sections {
  margin-bottom: 30px;
}

@media screen and (min-width: 1440px) {
  .footer::before {
    position: absolute;
    content: "";
    border-radius: 50%;
    background: radial-gradient(64% 64% at 27.25% 26.5%, rgba(255, 208, 208, 0.48) 0%, rgba(212, 93, 93, 0.25) 59.67%, rgba(167, 49, 49, 0.26) 78.59%, rgba(130, 25, 25, 0.23) 100%);
    box-shadow: 3px 6px 27px -10px rgba(223, 125, 125, 0.25);
    filter: blur(2.5px);
    backdrop-filter: blur(2px);
    top: -32%;
    left: 62%;
    width: 28px;
    height: 28px;
  }
}

.gifts__title {
  margin: 45px 0 20px;
}
@media screen and (min-width: 1440px) {
  .gifts__title {
    margin: 16px 0 40px 0;
    width: 290px;
    text-align: right;
  }
}

.gifts__title-primary {
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
}

.gifts__title-colored {
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  background: linear-gradient(90deg, #fcb045 36.99%, #fd1d1d 175%);
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
@media screen and (min-width: 1440px) {
  .gifts__title-colored {
    text-transform: uppercase;
  }
}

.gifts__button {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media screen and (min-width: 1440px) {
  .gifts__button {
    position: relative;
    width: 262px;
    height: 61px;
    background-color: #4077f3;
    color: #e4e5ea;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    text-decoration: none;
    margin-left: auto;
    margin-top: 30px;
  }
}

.hero__title {
  font-size: 36px;
  line-height: 44px;
  margin: 0;
  font-weight: 400;
}
@media screen and (min-width: 1440px) {
  .hero__title {
    font-weight: 400;
    font-size: 48px;
    line-height: 58px;
    width: 362px;
  }
}

.hero__colored-title {
  display: block;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 900;
  font-size: 36px;
  line-height: 44px;
  background: linear-gradient(92deg, #fcb045 27.14%, #ff3f78 121.36%), #e4e5ea;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
@media screen and (min-width: 1440px) {
  .hero__colored-title {
    display: inline-block;
    font-weight: 900;
    font-size: 48px;
    line-height: 58px;
  }
}

.hero__description {
  display: block;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  margin-top: 14px;
}
@media screen and (min-width: 1440px) {
  .hero__description {
    margin-top: 30px;
    width: 276px;
  }
}

.hero {
  position: relative;
}

.hero::before {
  position: absolute;
  content: "";
  width: 210px;
  height: 210px;
  background-color: #961a1a;
  border-radius: 50%;
  opacity: 0.05;
  top: 100%;
  left: -30%;
  box-shadow: 0 0 30px 30px #961a1a;
}
@media screen and (min-width: 1440px) {
  .hero::before {
    width: 310px;
    height: 310px;
    opacity: 0.15;
    top: 50px;
    left: -63%;
    box-shadow: 0 0 50px 50px #961a1a;
  }
}

@media screen and (min-width: 1440px) {
  .logo {
    text-decoration: none;
  }
}

@media screen and (min-width: 1440px) {
  .logo__caption {
    width: 140px;
    margin-top: 10px;
    font-family: "Montserrat", "Arial", sans-serif;
    color: #e4e5ea;
    font-weight: 300;
    font-size: 10px;
    line-height: 12px;
  }
}

.nav {
  padding: 20px 0;
  font-family: "Inter", "Arial", sans-serif;
}
@media screen and (min-width: 1440px) {
  .nav {
    padding: 0;
  }
}

.nav__list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  position: relative;
}
@media screen and (min-width: 1440px) {
  .nav__list {
    gap: 0 16px;
    justify-content: start;
  }
}

.nav__item {
  margin-right: 16px;
}
@media screen and (min-width: 1440px) {
  .nav__item {
    padding: -5px;
    margin: 0;
  }
}

.nav__item:nth-child(3n+4) {
  margin-right: 0;
}

.nav__item:last-child {
  display: none;
}
@media screen and (min-width: 1440px) {
  .nav__item:last-child {
    display: block;
  }
}

.nav__link {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
  font-size: 12px;
  line-height: 15px;
  font-weight: 500;
  text-transform: uppercase;
}
@media screen and (min-width: 1440px) {
  .nav__link {
    padding: 5px;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-transform: none;
  }
}

@media screen and (min-width: 1440px) {
  .nav__list::after {
    position: absolute;
    content: "";
    border-radius: 50%;
    background: radial-gradient(64% 64% at 27.25% 26.5%, rgba(255, 208, 208, 0.54) 0%, rgba(212, 93, 93, 0.26) 59.67%, rgba(167, 49, 49, 0.25) 78.59%, rgba(130, 25, 25, 0.25) 100%);
    box-shadow: 3px 6px 27px -10px rgba(223, 125, 125, 0.25);
    backdrop-filter: blur(2px);
    top: 22px;
    left: 182px;
    width: 83px;
    height: 83px;
  }
}

.options {
  padding: 0;
  list-style-type: none;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  text-transform: uppercase;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
}
@media screen and (min-width: 1440px) {
  .options {
    display: none;
  }
}

.options__item {
  margin: 0 10px 14px 26px;
  position: relative;
}

.options__item::before {
  content: "";
  background: linear-gradient(90deg, #fcb045 36.99%, #fd1d1d 100%);
  position: absolute;
  width: 10px;
  height: 1px;
  left: -16px;
  top: 38%;
  transform: translateY(-50%);
}

.section__title {
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  color: #656566;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  margin: 0 0 20px;
}
@media screen and (min-width: 1440px) {
  .section__title {
    font-size: 14px;
    line-height: 17px;
  }
}

.section__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.section__list--menu {
  column-count: 2;
  column-width: 120px;
  column-gap: 32px;
}

.section__item:not(:last-child) {
  margin-bottom: 10px;
}

.section__link {
  text-decoration: none;
  color: #e4e5ea;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
}
.section__link--desktop {
  display: none;
}
@media screen and (min-width: 1440px) {
  .section__link {
    font-size: 16px;
    line-height: 19px;
  }
  .section__link--mob {
    display: none;
  }
  .section__link--desktop {
    display: block;
  }
}

@media screen and (min-width: 1440px) {
  .section--contacts {
    text-align: right;
  }
}

.section__copyright {
  margin-top: 50px;
}
@media screen and (min-width: 1440px) {
  .section__copyright {
    display: flex;
    flex-direction: column;
  }
}

.button {
  padding: 20px 34px;
  display: block;
}